<template>
    <div style="padding-top: 2px;">
        <el-row class="step">
            <el-col :span="22">
                <el-steps :active="active" simple finish-status="success" style="height:20px; ">
                    <el-step :title="'当前项目:'+project_info.project_name"></el-step>
                    <el-step title="选择服务器&服务容器"></el-step>
                    <el-step title="完成"></el-step>
                </el-steps>
            </el-col>
        </el-row>
        <el-row :gutter="2">
            <!--选择服务器-->
            <div style="padding-top: 2px;" v-if="active === 1">
                <el-form :inline="true" :fit="false" ref="searchFormRef" :model="searchFormData" size="mini">
                    <el-form-item label="服务器名称" prop="server_name">
                        <el-input v-model="searchFormData.server_name" placeholder="请输入服务器名称"></el-input>
                    </el-form-item>
                    <el-form-item label="服务器编号" prop="server_no">
                        <el-input v-model="searchFormData.server_no" placeholder="请输入服务器编号"></el-input>
                    </el-form-item>
                    <div class="v-search-opt">
                        <div class="v-pagination">
                            <el-pagination
                                    @size-change="handleSizeChange"
                                    @current-change="handleCurrentChange"
                                    :current-page="searchFormData.pageNum"
                                    :page-size="searchFormData.pageSize"
                                    :total="searchFormData.pageCount"
                                    :page-sizes="[13, 20, 50, 100]"
                                    layout="total, sizes, prev, pager, next, jumper"
                            >
                            </el-pagination>
                        </div>
                        <div class="button-line">
                            <el-button size="mini" type="primary" @click="onSearch"><i class="iconfont icon-chaxun"></i> 查询</el-button>
                            <el-button size="mini" type="primary" @click="onReset"><i class="iconfont icon-chongzhi"></i> 重置</el-button>
                            <el-button size="mini" type="primary" @click="onNextStep"><i class="iconfont icon-xiayibu"></i> 下一步</el-button>
                        </div>
                    </div>
                </el-form>
                <hr>
                <el-table :data="tableData" :border="true" :stripe="true" size="mini" :height="getTableHeight(130)">
                    <el-table-column fixed type="index" :index="onIndexMethod(searchFormData.pageNum,searchFormData.pageSize)" label="序号" width="60"></el-table-column>
                    <el-table-column
                            :show-overflow-tooltip="true"
                            prop="server_name"
                            label="服务器名称"
                            width="160">
                    </el-table-column>
                    <el-table-column
                            prop="server_no"
                            label="服务器编号"
                            width="160">
                    </el-table-column>
                    <el-table-column
                            :formatter="format_server_state_code_id"
                            prop="server_state_code_id"
                            label="服务器状态"
                            width="90">
                    </el-table-column>
                    <el-table-column
                            prop="server_shelf_time"
                            label="上架时间"
                            width="90">
                    </el-table-column>
                    <el-table-column
                            prop="server_outer_ip"
                            label="外网ip"
                            width="140">
                    </el-table-column>
                    <el-table-column
                            prop="server_inner_ip"
                            label="内网ip"
                            width="140">
                    </el-table-column>
                    <el-table-column
                            prop="server_location"
                            label="服务器地址"
                            width="200"
                            :show-overflow-tooltip="true">
                    </el-table-column>
                    <el-table-column
                            prop="remark"
                            label="备注">
                    </el-table-column>
                    <el-table-column fixed="right" label="操作" width="180">
                        <template slot-scope="scope">
                            <el-col :span="11">
                                <el-button v-if="scope.row.is_project_server_link ==='0'" @click.native.prevent="on_add_project_server_link(scope.$index, tableData)" type="primary" size="mini">添加关联</el-button>
                                <el-button v-else @click.native.prevent="on_delete_project_server_link(scope.$index, tableData)" type="info" size="mini">移除关联</el-button>
                            </el-col>
                            <el-col :offset="2" :span="11">
                                <!--@click.native.prevent="on_click_link_service_container(scope.$index, tableData)"-->
                                <el-button v-if="scope.row.is_project_server_link ==='1'" @click.native.prevent="onView(module_name,scope.$index, tableData)" type="primary" size="mini">关联服务</el-button>
                                <el-button v-else type="warning" size="mini">关联服务</el-button>
                            </el-col>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <div style="padding-top: 2px;" v-if="active === 2">
                <h1>这里还能写点啥？？？？？？？？？</h1>
            </div>
        </el-row>
    </div>
</template>

<script>
    export default {
        name: "projectServiceContainerConfList",
        data() {
            return {
                module_name: 'projectServiceContainerConf',
                refresh_page_method: 'refresh_' + this.module_name,
                project_id: '',
                active: 1,
                server_state_code_list: null,
                project_type_code_list: null,
                project_env_code_list: null,
                project_state_code_list: null,
                searchFormData: {
                    pageNum: 1,
                    pageSize: 13,
                    pageCount: 0
                },
                selected_server_table_data: [], /*已选服务器列表*/
                selected_container_table_data: [], /*已选服务器容器列表*/
                tableData: [],
                selected_server_id: '',
                project_info: {
                    project: '',
                    project_name: '',
                    project_type_code_id: '',
                    project_env_code_id: '',
                    project_context: '',
                    project_version: '',
                    project_default_root_path: '',
                    remark: ''
                }
            }
        },
        async mounted() {
            this.project_id = this.$route.params.project_id;
            this.server_state_code_list = await this.$setCodeList(this.server_state_code_list, 'server_state');
            this.project_type_code_list = await this.$setCodeList(this.project_type_code_list, 'project_type');
            this.project_env_code_list = await this.$setCodeList(this.project_env_code_list, 'project_env');
            this.project_state_code_list = await this.$setCodeList(this.project_state_code_list, 'project_state');
            this.search_project_info();
            this.search();
        },
        methods: {
            handleSizeChange(val) //修改单页显示条数
            {
                this.searchFormData.pageSize = val;
                this.search();
            },
            handleCurrentChange(val) //当前页修改
            {
                this.searchFormData.pageNum = val;
                this.search();
            },
            onReset() // 重置功能
            {
                this.$refs.searchFormRef.resetFields();
                this.search();
            },
            format_server_state_code_id(row, column, cellValue) {
                return this.$formatCodeValue(row, this.server_state_code_list, 'server_state_code_id')
            },
            onSearch() // 查询按钮
            {
                this.searchFormData.pageNum = 1;
                this.search();
            },
            onNextStep() {
                this.active = 2;
            },
            async search() // 查询按钮
            {
                /*查询服务器列表*/
                this.search_project_server_list()
            },
            async search_project_info()/*查询项目信息*/ {
                let searchData = {
                    "project_id": this.project_id
                };
                let data = await this.$search(searchData, 'OAMP_search_project_info_by_id')
                this.project_info = data.data;
            },
            async search_project_server_list() {
                let searchData = this.searchFormData;
                searchData['project_id'] = this.project_id;
                let data = await this.$search(searchData, 'OAMP_search_project_server_list');
                this.searchFormData.pageCount = data.pageCount;
                this.tableData = data.data;
            },
            async on_add_project_server_link(index, rows)/*添加项目-服务器关联信息*/ {
                this.active = 1;
                let item = {
                    link_id: this.project_id, /*项目id*/
                    linked_id: rows[index]._id, /*服务器id*/
                    link_type_code_id: '1'
                };
                let data = await this.$insert(item, 'OAMP_insert_link_info');
                if (data !== null) {
                    this.search();
                }
            },
            async on_delete_project_server_link(index, rows)/*移除项目-服务器关联信息*/ {
                this.active = 1;
                let item = {
                    '_id': rows[index].link_info_id/*关联信息id*/
                };
                let data = await this.$del(item, 'OAMP_delete_link_info');
                if (data === true) {
                    this.search();
                } else {
                    this.$message.error('删除关联服务器信息失败')
                }
            },

            async search_selected_server_list() {
                let item = {
                    'project_id': this.project_id
                };
                let data = this.selected_server_table_data = await this.$search(item, 'OAMP_search_server_list_by_project_id');
                this.selected_server_table_data = data.data;
                this.search();
            },
            async on_link_service_container(index, rows)/*关联容器*/ {
                this.active = 1;
                this.tableData = [];
                this.selected_server_id = rows[index]._id;
                let item = this.searchFormData;
                item['belong_to_server_id'] = this.selected_server_id;
                let data = await this.$search(item, 'OAMP_search_service_container_list');
                this.tableData = data.data;
            },
            on_click_link_service_container(index, rows)/*点击关联服务容器按钮*/ {
                this.active = 2;
            },
            on_add_project_service_link(index, rows)/*添加项目和服务容器关联*/ {

            },
            on_delete_project_service_link(index, rows)/*删除项目和服务容器关联*/ {

            }
        }
    }
</script>

<style scoped>
    .gray {
        color: #333333;
    }

    .project_title {
        padding: 10px;
        background: #CCCCCC;
        font-size: 24px;
        text-align: center;
        border-radius: 15px;
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
    }

    .project_tip {
        background-color: #ffffff;
        padding-top: 2px;
        padding-right: 4px;
    }

    .step {
        background-color: #ffffff !important
    }
</style>


