<template>
    <el-dialog title="服务容器选择列表"
               width="1250px"
               :visible="pageInfo.isShow"
               :modal-append-to-body="false"
               :close-on-click-modal="false"
               :center="true"
               :lock-scroll="false"
               @close="onClose(module_name)"
    >
        <el-form :inline="true" ref="searchFormRef" :model="searchFormData" size="mini">
            <el-form-item label="容器名称" prop="service_container_name">
                <el-input v-model="searchFormData.service_container_name" placeholder="请输入容器名称"></el-input>
            </el-form-item>
            <el-form-item label="创建时间" prop="createTime">
                <el-date-picker
                        v-model="searchFormData.createTime"
                        type="daterange"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期">
                </el-date-picker>
            </el-form-item>
            <div class="v-center">
                <div class="v-inline">
                    <el-button size="mini" type="primary" @click="onSearch"><i class="iconfont icon-chaxun"></i> 查询</el-button>
                    <el-button size="mini" type="primary" @click="onReset"><i class="iconfont icon-chongzhi"></i> 重置</el-button>
                    <el-button size="mini" type="primary" @click="onClose(module_name)"><i class="iconfont icon-guanbi"></i> 关闭</el-button>
                </div>
            </div>
        </el-form>
        <hr>
        <el-table :data="tableData" :border="true" :stripe="true" size="mini" :height="getTableHeight(360)">
            <el-table-column fixed type="index" :index="onIndexMethod(searchFormData.pageNum,searchFormData.pageSize)" label="序号" width="60"></el-table-column>
            <el-table-column
                    :formatter="format_service_container_state"
                    prop="service_container_state_code_id"
                    label="运行状态"
                    width="100">
            </el-table-column>
            <el-table-column
                    :show-overflow-tooltip="true"
                    prop="belong_to_server_id"
                    label="所属服务器id"
                    width="280">
            </el-table-column>
            <el-table-column
                    prop="createTime"
                    label="创建时间"
                    width="160">
            </el-table-column>
            <el-table-column
                    :show-overflow-tooltip="true"
                    prop="service_container_name"
                    label="容器名称"
                    width="160">
            </el-table-column>
            <el-table-column
                    prop="service_container_port"
                    label="容器端口"
                    width="80">
            </el-table-column>
            <el-table-column
                    :formatter="format_service_container_type"
                    prop="service_container_type_code_id"
                    label="容器类型">
            </el-table-column>
            <el-table-column fixed="right" label="操作" width="120">
                <template slot-scope="scope">
                    <el-col :span="24">
                        <el-col :span="11">
                            <el-button v-if="scope.row.is_service_container_link ==='0'" @click.native.prevent="on_add_project_service_container_link(scope.$index, tableData)" type="primary" size="mini">添加容器关联</el-button>
                            <el-button v-else-if="scope.row.is_service_container_link ==='1'" @click.native.prevent="on_delete_project_service_container_link(scope.$index, tableData)" type="info" size="mini">移除容器关联</el-button>
                            <el-button v-else  type="danger" size="mini">容器已被关联</el-button>
                        </el-col>
                    </el-col>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="searchFormData.pageNum"
                :page-size="searchFormData.pageSize"
                :total="searchFormData.pageCount"
                :page-sizes="[13, 20, 50, 100]"
                layout="total, sizes, prev, pager, next, jumper"
        >
        </el-pagination>
    </el-dialog>
</template>
<script>
    export default {
        name: 'selectServiceContainerList',
        data() {
            return {
                module_name: 'projectServiceContainerConf',
                refresh_page_method: 'refresh_' + this.module_name,
                service_container_type_code_list: null,
                service_container_state_code_list: null,
                project_id: '',
                pageData: {},
                searchFormData: {
                    pageNum: 1,
                    pageSize: 13,
                    pageCount: 0
                },
                tableData: []
            }
        },
        watch:{
            pageData(){
                this.search();
            }
        },
        created() {
            this.$store.state.Apps.$on(this.refresh_page_method, () => {
                this.search();
            });
        },
        computed: {
            pageInfo() {
                this.project_id = this.$route.params.project_id;
                return this.$setPageInfo(this.module_name);
            }
        },
        async mounted() {
            this.service_container_type_code_list = await this.$setCodeList(this.service_container_type_code_list, 'service_container_type'); // 服务容器类型
            this.service_container_state_code_list = await this.$setCodeList(this.service_container_state_code_list, 'service_container_state'); // 服务容器类型
        },
        methods: {
            format_service_container_type(row, column, cellValue) /*格式化项目类型信息*/ {
                return this.$formatCodeValue(row, this.service_container_type_code_list, 'service_container_type_code_id')
            },
            format_service_container_state(row, column, cellValue) /*格式化项目类型信息*/ {
                return this.$formatCodeValue(row, this.service_container_state_code_list, 'service_container_state_code_id')
            },
            handleSizeChange(val) //修改单页显示条数
            {
                this.searchFormData.pageSize = val;
                this.search();
            },
            handleCurrentChange(val) //当前页修改
            {
                this.searchFormData.pageNum = val;
                this.search();
            },
            onReset() // 重置功能
            {
                this.$refs.searchFormRef.resetFields();
                this.search();
            },
            onSearch() // 查询按钮
            {
                this.searchFormData.pageNum = 1;
                this.search();
            },
            async search() {
                let searchData = this.searchFormData;
                searchData['project_id'] = this.project_id;
                searchData['belong_to_server_id'] = this.pageData._id;
                let data = await this.$search(searchData, 'OAMP_search_project_server_container_list');
                this.searchFormData.pageCount = data.pageCount;
                this.tableData = data.data;
            },
            async on_add_project_service_container_link(index,rows)/*添加项目服务容器关联*/ {
                let item = {
                    link_id: this.project_id, /*项目id*/
                    linked_id: rows[index]._id, /*服务器id*/
                    link_type_code_id: '2'
                };
                let data = await this.$insert(item, 'OAMP_insert_link_info');
                if (data !== null) {
                    this.search();
                }
            },
            async on_delete_project_service_container_link(index,rows)/*删除项目服务容器关联*/ {
                let item = {
                    '_id': rows[index].link_info_id/*关联信息id*/
                };
                let data = await this.$del(item, 'OAMP_delete_link_info');
                if (data === true) {
                    this.search();
                } else {
                    this.$message.error('删除关联服务器信息失败')
                }
            },

        }
    }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>


